@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
header {
  background: var(--navbar-color);
  transition: background var(--transition-delay);
  width: 100%;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.8);
  font-family: 'Roboto', sans-serif;
  position: fixed;
  z-index: 99;
  top: 0;
}

nav {
  height: 64px;
  margin: auto;
  margin-left: 5%;
  width: 95%;
  display: flex;
  align-self: center;
  justify-content: space-between;
}

nav .nav-btn-group {
  display: flex;
}

nav .nav-btn {
  height: 100%;
  width: 64px;
  display: flex;
  background: rgba(0, 0, 0, 0);
  transition: background var(--transition-delay);
  border: none;
  transition: font-size 0.5s ease-in;
  text-align: center;
  border-radius: 0px;
}

nav .nav-btn:hover {
  background: var(--nav-btn-hover-color);
  transition: background var(--transition-delay);
}

nav .nav-btn h6 {
  margin: auto;
  font-size: 12px;
  color: var(--navbar-text-color);
  transition: color var(--transition-delay);
  font-weight: 500;
}

nav h1 {
  font-size: 20px;
  color: var(--navbar-text-color);
  transition: color var(--transition-delay);
  font-weight: 400;
  margin-top: auto;
  margin-bottom: auto;
  transition: font-size 0.5s;
}

nav .vertical-bar {
  margin: auto;
  height: 40%;
  width: 1px;
  background: var(--navbar-divider-color);
  transition: background var(--transition-delay);
}

@media screen and (min-width: 768px) {
  nav h1 {
    font-size: 22px;
  }

  nav .nav-btn h6 {
    font-size: 16px;
  }

  nav .nav-btn {
    width: 120px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: 'Roboto', sans-serif;
}

.content {
  margin: auto;
  top: 64px;
  position: relative;
  width: 75%;
  min-height: 83vh;
}

main {
  width: 100%;
  margin-top: 6vh;
}

@media screen and (max-width: 768px) {
  .content {
    width: 100%;
    transition: all 0.5s;
  }

  main {
    margin-top: 0;
    transition: all 0.5s;
  }
}

.icon-only-div {
  display: flex;
  width: 100%;
  height: 100%;
  margin-top: 6vh;
  flex-direction: column;
}

.middle-text {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  color: var(--main-font-color);
  opacity: 0.75;
}

.middle-icon {
  color: var(--main-font-color);
  margin: auto;
  width: 100px;
  height: 100px;
  margin-top: 6vh;
  opacity: 0.75;
}

.svg-inline--fa.fa-w-16 {
  width: 100px;
}

footer {
  width: 100%;
  height: 48px;
  display: flex;
  flex-direction: column;
  margin-top: 96px;
  background: var(--footer-background);
  transition: background var(--transition-delay);
}

.horizontal-line {
  height: 1px;
  width: 100%;
  background: var(--footer-horizontal-divider-color);
  transition: background var(--transition-delay);
}

.footer-inner-container {
  margin: auto;
  height: 47px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 70%;
}

.vertical-line {
  margin: auto;
  height: 40%;
  background: var(--footer-vertical-divider-color);
  transition: background var(--transition-delay);
  width: 1px;
}

.footer-inner-container h4 {
  width: 50%;
  margin: auto;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  color: var(--footer-font-color);
  transition: color var(--transition-delay);
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  footer {
    margin-top: 64px;
  }
}

.loading-animation {
  -webkit-animation-name: rotate-animation;
          animation-name: rotate-animation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes rotate-animation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate-animation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --transition-delay: 300ms;
}

.dark-mode {
  --main-bg-color: #121212;
  --navbar-color: #1f1f1f;
  --navbar-text-color: #bb86fc;
  --navbar-divider-color: #bb86fc;
  --nav-btn-hover-color: #2e2e2e;
  --card-background: #1d1d1d;
  --main-font-color: #ffffff;
  --footer-font-color: var(--navbar-text-color);
  --footer-background: var(--navbar-color);
  --footer-horizontal-divider-color: #2e2e2e;
  --footer-vertical-divider-color: var(--navbar-divider-color);
  --link-color: var(--navbar-text-color);
}

.light-mode {
  --main-bg-color: rgba(245, 246, 250, 255);
  --navbar-color: #6200ee;
  --navbar-text-color: #ffffff;
  --navbar-divider-color: #3700b3;
  --nav-btn-hover-color: #3700b3;
  --card-background: #ffffff;
  --main-font-color: #000000;
  --footer-font-color: rgba(0, 0, 0, 0.6);
  --footer-background: var(--card-background);
  --footer-horizontal-divider-color: rgba(0, 0, 0, 0.3);
  --footer-vertical-divider-color: rgba(0, 0, 0, 0.3);
  --link-color: default;
}

body {
  margin: 0;
  background: var(--main-bg-color);
  transition: background 300ms;
  transition: background var(--transition-delay);
}

a {
  color: var(--link-color);
  transition: color 300ms;
  transition: color var(--transition-delay);
}

